import { useQuery } from '@apollo/client';
import { css } from '@emotion/react';
import { RootStateType } from '@Redux/reducer';
import { useEffect, useMemo, useState } from 'react';
import { useSelector } from 'react-redux';
import AtomButton from '../AtomButton';
import AtomIcon from '../AtomIcon';
import AtomInput from '../AtomInput';
import AtomText from '../AtomText';
import AtomWrapper from '../AtomWrapper';

import { BUTTON_ICON } from './css/button';
import { INPUT_SELECT, INPUT_TEXT } from './css/inputs';

import IsLoadingTable from './AtomTableLoading';
import LoaderSmall from './LoaderSmall';

import { LISTPRODUCTS } from './apollo/query/Products';
import {
  AtomTableContainerStyled,
  AtomTableStyled,
  AtomTbodyStyled,
  AtomTfooterStyled,
  AtomTheaderStyled,
  TDStyled,
  TRStyled,
} from './styled';
import { AtomTableTypes, ParamsTypes } from './types';

const OptionsTake = [
  {
    id: 'takeKey0',
    label: '5',
    value: '5',
  },
  {
    id: 'takeKey1',
    label: '10',
    value: '10',
  },
  {
    id: 'takeKey2',
    label: '25',
    value: '25',
  },
  {
    id: 'takeKey3',
    label: '50',
    value: '50',
  },
];

type ISort = {
  [key: string]: 'ASC' | 'DESC' | 'NONE';
};

const AtomTableQuery = <T extends object, P extends object>(
  props: AtomTableTypes<T, P>
) => {
  const {
    customCSS,
    columnscss,
    QUERY,
    data,
    searchBar,
    options,
    columns,
    onCompleted,
    onError,
    onLoading,
    onUpdated,
    id,
  } = props;
  const [take, setTake] = useState(10);
  const [skip, setSkip] = useState(0);
  const [order, setOrder] = useState<ISort>({});
  const [search, setSearch] = useState('');
  const [getParams, setParams] = useState({} as ParamsTypes);
  const [getData, setData] = useState<T[]>([]);
  const [loading, setLoading] = useState(true);
  const customize = useSelector((state: RootStateType) => state.customize);

  const getOrder = useMemo(() => {
    const orderArray = Object.entries(order);
    if (orderArray.length === 0) return {};
    const orderArrayFiltered = orderArray.filter((item) => item[1] !== 'NONE');
    return {
      order: Object.fromEntries(orderArrayFiltered),
    };
  }, [order]);

  const getSearch = useMemo(() => {
    const itemFiltered = columns.filter((item) => item?.searchable);
    if (search === '' || itemFiltered.length === 0) return null;
    return {
      filter: itemFiltered?.reduce(
        (acc, item) => ({
          or: [
            ...acc.or,
            {
              [item.key]: {
                contains: search,
              },
            },
          ],
        }),
        {
          or: [],
        }
      ),
    };
  }, [search]);

  const getFilter = useMemo(() => {
    if (options?.variables?.filter || getSearch?.filter) {
      return {
        filter: {
          ...options?.variables?.filter,
          ...getSearch?.filter,
        },
      };
    }
    return null;
  }, [getParams, getSearch, options]);

  const QUERYGET = useQuery<P>(QUERY ?? LISTPRODUCTS, {
    ...options,
    variables: {
      ...options?.variables,
      ...getFilter,
      skip: take * skip,
      take,
      ...getOrder,
    },
    onCompleted: (e) => {
      setLoading(false);
      onCompleted?.(e);
    },
    onError: (error) => {
      setLoading(false);
      onError?.(error);
    },
  });

  useEffect(() => {
    onLoading?.(QUERYGET?.loading);
  }, [QUERYGET?.loading]);

  useEffect(() => {
    onUpdated?.(QUERYGET);
  }, [QUERYGET]);

  const ErrorMessages = useMemo(() => {
    const getErros = QUERYGET?.error?.networkError as unknown as {
      result: { errors: { message: string }[] };
    };
    return getErros?.result?.errors ?? [];
  }, [QUERYGET?.error]);

  const getArrayPages = useMemo(
    () =>
      Array.from(
        { length: Math.ceil(getParams?.totalCount / take) },
        (_, i) => i
      ),
    [getParams?.totalCount, take]
  );

  const getArrayPagesShow = useMemo(
    () =>
      getArrayPages.filter(
        (item) =>
          item >= Math.min(skip - 2, getArrayPages.length - 5) &&
          item <= Math.max(skip + 2, 4)
      ),
    [getArrayPages, skip]
  );

  useEffect(() => {
    const get = Object.values(QUERYGET?.data ?? ({} as ParamsTypes))?.[0];
    const params = {
      pageInfo: get?.pageInfo ?? getParams?.pageInfo,
      totalCount: get?.totalCount ?? getParams?.totalCount,
    } as ParamsTypes;

    const dataTable = data?.(QUERYGET?.data) ?? getData;

    setParams(params);
    setData(dataTable);
  }, [QUERYGET, QUERY, options]);

  const isSearchBar = useMemo(() => {
    const isSearch = columns.filter((item) => item?.searchable).length > 0;
    return searchBar || isSearch;
  }, [searchBar, columns]);

  return (
    <AtomTableContainerStyled
      css={css`
        width: 100%;
        ${customCSS}
      `}
    >
      {isSearchBar && (
        <AtomTheaderStyled>
          {searchBar ? (
            <>{searchBar}</>
          ) : (
            <AtomWrapper
              customCSS={css`
                flex-direction: row;
                justify-content: flex-end;
                align-items: center;
                gap: 10px;
              `}
            >
              <AtomText>Buscar </AtomText>
              <AtomInput
                value={search}
                onChange={(e) => setSearch(e.target.value)}
                customCSS={css`
                  ${INPUT_TEXT}
                  input {
                    font-size: 10px;
                    font-weight: 700;
                    color: #424242;
                    height: 30px;
                    min-width: 180px;
                  }
                `}
              />
            </AtomWrapper>
          )}
        </AtomTheaderStyled>
      )}
      <AtomTableStyled
        id={id}
        cl={columns?.length}
        customCSST={props?.customCSST}
      >
        {ErrorMessages?.length > 0 && (
          <AtomWrapper
            customCSS={css`
              align-items: center;
              justify-content: center;
              position: absolute;
              width: 100%;
              height: 100%;
              background-color: #fcfcfc6d;
              backdrop-filter: blur(5px);
              z-index: 1;
              gap: 20px;
            `}
          >
            <AtomText>
              {ErrorMessages?.map((e) => (
                <AtomText
                  key={e?.message}
                  customCSS={css`
                    font-size: 16px;
                    color: #ff0000;
                    font-weight: 600;
                  `}
                >
                  {e.message}
                </AtomText>
              ))}
            </AtomText>
          </AtomWrapper>
        )}
        <IsLoadingTable loading={loading} columns={columns} take={take}>
          <AtomTbodyStyled>
            <TRStyled className="head">
              {columns?.map((e, i) => (
                <th key={`header ${i + 1}`}>
                  <AtomWrapper
                    customCSS={css`
                      padding-right: 10px;
                      align-items: center;
                      justify-content: space-between;
                      flex-direction: row;
                      width: 100%;
                    `}
                  >
                    {e.title}
                    {e?.sortable && (
                      <AtomWrapper
                        customCSS={css`
                          width: max-content;
                        `}
                      >
                        <AtomButton
                          onClick={() => {
                            setOrder({
                              ...order,
                              [e.key]:
                                order?.[e.key] === 'ASC' ? 'NONE' : 'ASC',
                            });
                          }}
                          customCSS={css`
                            ${BUTTON_ICON}
                            transform: rotate(90deg)!important;
                            min-height: 0;
                          `}
                        >
                          <AtomIcon
                            height="10px"
                            width="10px"
                            color={
                              order?.[e.key] === 'ASC' ? '#565656' : '#c0c0c0'
                            }
                            icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/MDC-0001/svg/chevron-left-solid%20(2).svg"
                          />
                        </AtomButton>
                        <AtomButton
                          onClick={() => {
                            setOrder({
                              ...order,
                              [e.key]:
                                order?.[e.key] === 'DESC' ? 'NONE' : 'DESC',
                            });
                          }}
                          customCSS={css`
                            ${BUTTON_ICON}
                            transform: rotate(90deg)!important;
                            min-height: 0;
                          `}
                        >
                          <AtomIcon
                            height="10px"
                            width="10px"
                            color={
                              order?.[e.key] === 'DESC' ? '#565656' : '#c0c0c0'
                            }
                            icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/MDC-0001/svg/chevron-right-solid%20(7).svg"
                          />
                        </AtomButton>
                      </AtomWrapper>
                    )}
                  </AtomWrapper>
                </th>
              ))}
            </TRStyled>

            {getData?.map((e, i: number) => (
              <TRStyled
                key={`row${i + 1}-${skip}-${take}`}
                animate={{
                  opacity: 1,
                }}
                initial={{ opacity: 0 }}
                transition={{ delay: i * 0.03, duration: 0, ease: 'easeOut' }}
                css={columnscss?.(e)}
              >
                {columns?.map((td, j) => (
                  <TDStyled
                    key={`cell ${i + 1} ${j + 1}`}
                    {...td}
                    title={`${td?.key} ${i + 1}`?.toUpperCase()}
                  >
                    {td.view(e)}
                  </TDStyled>
                ))}
              </TRStyled>
            ))}
          </AtomTbodyStyled>
        </IsLoadingTable>
      </AtomTableStyled>

      <AtomTfooterStyled>
        <AtomWrapper
          customCSS={css`
            display: flex;
            table-layout: inherit;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
          `}
        >
          <AtomWrapper
            customCSS={css`
              flex-direction: row;
              align-items: center;
              gap: 20px;
              width: max-content;
            `}
          >
            <AtomWrapper
              customCSS={css`
                span {
                  font-size: 12px;
                  font-weight: 600;
                  color: #383838;
                }
              `}
            >
              {getParams?.totalCount && (
                <AtomText>
                  Pagina {skip + 1} de{' '}
                  {Math.ceil((getParams?.totalCount ?? 1) / take) ?? (
                    <>Cargando...</>
                  )}
                </AtomText>
              )}
              <AtomText>
                Elementos {getParams?.totalCount ?? getData.length ?? 1}
              </AtomText>
            </AtomWrapper>

            {getArrayPagesShow.length > 0 && (
              <AtomInput
                id="take"
                value={take}
                options={OptionsTake}
                onChange={(e) => {
                  setTake(Number(e?.target?.value));
                  setSkip(0);
                }}
                type="select"
                customCSS={css`
                  ${INPUT_SELECT}
                  select {
                    height: 30px;
                    font-size: 10px;
                    min-width: 150px;
                  }
                `}
              />
            )}
          </AtomWrapper>

          {getArrayPagesShow.length > 0 && (
            <AtomWrapper
              customCSS={css`
                flex-direction: row;
                align-items: center;
                gap: 10px;
                width: max-content;
              `}
            >
              <AtomWrapper
                customCSS={css`
                  flex-direction: row;
                  align-items: center;
                  gap: 10px;
                `}
              >
                <AtomButton
                  onClick={() =>
                    setSkip(getParams?.pageInfo?.hasPreviousPage ? skip - 1 : 0)
                  }
                  customCSS={css`
                    ${BUTTON_ICON}
                  `}
                >
                  <AtomIcon
                    height="16px"
                    width="16px"
                    color={
                      getParams?.pageInfo?.hasPreviousPage
                        ? customize?.colors?.primary ?? '#f1576c'
                        : '#c0c0c0'
                    }
                    icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/MDC-0001/svg/chevron-left-solid%20(2).svg"
                  />
                </AtomButton>
                <AtomWrapper
                  customCSS={css`
                    flex-direction: row;
                    align-items: center;
                    gap: 10px;
                  `}
                >
                  {getArrayPagesShow.map((i) => (
                    <AtomButton
                      key={`page ${i}`}
                      onClick={() => setSkip(i)}
                      customCSS={css`
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        padding: 6px 12px;
                        font-size: 12px;
                        background-color: ${skip === i
                          ? customize?.colors?.primary ?? '#f1576c'
                          : 'white'};
                        color: ${skip === i ? 'white' : '#383838'};
                      `}
                    >
                      {`${i + 1}`}
                    </AtomButton>
                  ))}
                </AtomWrapper>
                <AtomButton
                  onClick={() =>
                    setSkip(getParams?.pageInfo?.hasNextPage ? skip + 1 : skip)
                  }
                  customCSS={css`
                    ${BUTTON_ICON}
                  `}
                >
                  <AtomIcon
                    height="16px"
                    width="16px"
                    color={
                      getParams?.pageInfo?.hasNextPage
                        ? customize?.colors?.primary ?? '#f1576c'
                        : '#c0c0c0'
                    }
                    icon="https://storage.googleapis.com/cdn-bucket-ixulabs-platform/MDC-0001/svg/chevron-right-solid%20(7).svg"
                  />
                </AtomButton>
              </AtomWrapper>
              <AtomWrapper
                customCSS={css`
                  width: 20px;
                  height: 20px;
                  justify-content: center;
                  align-items: center;
                `}
              >
                {QUERYGET?.loading && <LoaderSmall />}
              </AtomWrapper>
            </AtomWrapper>
          )}
        </AtomWrapper>
      </AtomTfooterStyled>
    </AtomTableContainerStyled>
  );
};
export default AtomTableQuery;
